.card {
	width: 30rem;
	height: 35rem;
	background: rgba(181, 194, 240, 0.187);
	padding: .4em;
	border-radius: 10px;
	box-shadow: 2px 4px 4px rgba(90, 37, 3, 0.166);

	.card-image {
		height: 50%;
		background-color: red;

		img {
			//background-color: rgb(236, 236, 236);
			width: 100%;
			height: 100%;
			border-radius: 6px 6px 0 0;
			object-fit: cover;
			//aspect-ratio: 4/3;
		}
	}
}

.card:hover {
	transform: scale(0.98);
	cursor: pointer;
}

.category {
	//text-transform: ;
	font-size: 2rem;
	font-weight: 600;
	color: rgb(63, 121, 230);
	padding: 3rem 7px 0;
}

.category:hover {
	cursor: pointer;
}

.heading {
	font-weight: 600;
	color: rgb(88, 87, 87);
	padding: 7px;
}

.heading:hover {
	cursor: pointer;
}

.author {
	color: gray;
	font-weight: 400;
	font-size: 11px;
	padding-top: 20px;
}

.name {
	font-weight: 600;
}

.name:hover {
	cursor: pointer;
}